.wrapper{
  width: 87%;
  margin: 0 auto;
  @media (min-width: 768px) {
    width: 92%;
  }
  @media (min-width: 1440px) {
    width: 1220px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
